$key-frames-ms: 600ms, 500ms, 400ms, 300ms, 200ms, 100ms;

@each $current-ms in $key-frames-ms {
  $i: index($key-frames-ms, $current-ms);
  .preloader__inner label:nth-child(#{$i}) {
    animation: loader 3s $current-ms infinite ease-in-out;
  }
}

.preloader {
  position: fixed;
  background: #c5e7f8;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: opacity 0.55s, visibility 0.55s;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
  img {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
  &_active {
    opacity: 1;
    visibility: visible;
  }
  &__inner {
    transform: translateY(-50%);
    top: 50%;
    position: absolute;
    width: calc(100% - 200px);
    color: #FFF;
    padding: 0 100px;
    text-align: center;
    label {
      font-size: 2em;
      padding: 1%;
      opacity: 0;
      display: inline-block;
      &:after {
        content: '•';
      }
    }
  }
}

@keyframes loader {
  0% {
    opacity: 0;
    transform: translateX(-300px) scale(1);
  }
  33% {
    opacity: 1;
    transform: translateX(0px) scale(2);
  }
  66% {
    opacity: 1;
    transform: translateX(0px) scale(1);
  }
  100% {
    opacity: 0;
    transform: translateX(300px) scale(2);
  }
}