.order {
  padding: 34px 0 37px;
  background-color: #c5e7f8;
  text-align: center;
  &__title {
    @include col();
    @include size(12);
    margin-bottom: 11px;
    h2 {
      font-size: 32px;
      @include xs-block() {
        font-size: 26px;
      }
      span {
        display: block;
        font-weight: 400;
      }
    }
  }
  &__link {
    @include col();
    @include size(12);
  }
}