.accordion {
  li {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  &__question {
    font-size: 22px;
    font-weight: 700;
    background-color: $color-accent-second;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 5px 22px 5px 14px;
    cursor: pointer;
    @include xs-block() {
      font-size: 16px;
    }
    @include sm-block() {
      font-size: 18px;
    }
    &::after {
      content: '';
      display: block;
      margin-top: 7px;
      margin-left: auto;
      flex-shrink: 0;
      @include triangle(to bottom, 14px, 12px, #fff);
    }
    &::before {
      content: '?';
      box-sizing: border-box;
      display: inline-flex;
      width: 36px;
      height: 36px;
      align-items: center;
      justify-content: center;
      font-family: $font-second;
      font-size: 22px;
      border: 2px dashed #fff;
      border-radius: 50%;
      flex-shrink: 0;
    }
    &_active {

    }
    p {
      margin: 0;
      padding: 0 15px;
    }
    //span {
    //  box-sizing: border-box;
    //  display: inline-flex;
    //  width: 36px;
    //  height: 36px;
    //  align-items: center;
    //  justify-content: center;
    //  font-family: $font-second;
    //  font-size: 22px;
    //  border: 2px dashed #fff;
    //  border-radius: 50%;
    //  margin-right: 15px;
    //}
  }
  &__answer {
    display: none;
    font-size: 18px;
    background-color: #fff;
    margin: 8px 0 0;
    padding: 8px 17px 10px;
  }
}