.header {
  width: 100%;
  position: fixed;
  z-index: 9;
  top: 0;
  &__container {
    @include col();
    @include size(12);
    @include md-block() {
      justify-content: space-between;
		padding: 10px 28px;
    }
    @include xs-block() {
      margin: 0;
      width: 100%;
    }
    background-color: #fff;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    align-items: center;
    padding: 16px 28px;
  }
  &__logo {
    margin-right: 64px;
  }
  &__menu {
    display: block;
    transition: all, 0.3s;
    @include md-block() {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
    ul {
      @include md-block() {
        transform: translate3d(-100%,0,0);
        background-color: #90a8b5;
        padding: 20px 40px;
        height: 100vh;
        width: fit-content;
      }
      &.animated {
        opacity: 1;
      }
    }
    li {
      display: inline-block;
      @include md-block() {
        display: block;
      }
      &:not(:last-child) {
        margin-right: 30px;
        @include md-block() {
          margin: 0 0 20px 0;
        }
      }
    }
    a {
      color: inherit;
      text-decoration: none;
		@include md-block() {
			color: #fff;
			display: block;
			border-radius: 3px;
			padding: 6px 25px;
		}
      &:hover, &.is_active {
        color: $color-accent;
        text-decoration: underline;
		  @include md-block() {
			  color: #000;
			  background-color: #fff;
			  text-decoration: none;
		  }
      }
    }
    &.animated {
      opacity: 1;
    }
  }
  &__trigger {
    display: none;
    transition: all 0.2s;
    background: transparent;
    width: 26px;
    height: 26px;
    @include md-block() {
      display: block;
    }
    span, span::before, span::after {
      display: block;
      transition: all 0.2s;
      width: 26px;
      height: 3px;
      background: $color-accent;
      border-radius: 20px;
      border: none;
    }
    span {
      position: relative;
      margin-top: 0;
      margin-left: 0;
      &::before {
        content: "";
        top: -8px;
        left: 0;
        position: absolute;
      }
      &::after {
        content: "";
        top: 8px;
        left: 0;
        position: absolute;
      }
    }
    &_active {
      span {
        transform: rotate(-45deg);
        &::after {
          top: 0;
          transform: rotate(90deg);
        }
        &::before {
          display: none;
        }
      }
    }
  }
}
