.title {
  @include col();
  @include size(12);
  text-align: center;
  h2 {
    font-size: 42px;
    margin-top: 0;
    @include sm-block() {
      font-size: 32px;
    }
    @include xs-block() {
      font-size: 25px;
    }
    span {
      display: block;
      font-weight: 400;
    }
  }
}