.features {
  padding: 49px 0 67px;
  &__items {
    margin-top: 15px;
  }
  &__item {
    @include col-padding();
    @include size(6);
    @include size-sm(12);
    padding-top: 24px;
    margin-bottom: 38px;
    @include sm-block() {
      padding-bottom: 24px;
      margin-bottom: 0;
    }
    &:nth-child(odd) {
      .features__inner {
        margin-left: auto;
      }
    }
    &:nth-child(even) {
      .features__inner {
        margin-left: 15px;
        @include md-block() {
          margin: 0;
        }
      }
    }
    &:first-child {
      background-image: url(../img/features-item-1-bg.jpg);
      background-repeat: no-repeat;
      background-position: calc(100% - 15px) 0;
      height: 313px;
      @include sm-block() {
        height: auto;
      }
    }
    &:nth-child(4) {
      background-image: url(../img/features-item-4-bg.jpg);
      background-repeat: no-repeat;
      background-position: left 0;
      height: 345px;
      @include sm-block() {
        height: auto;
      }
    }
    &:nth-child(5) {
      background-image: url(../img/features-item-5-bg.jpg);
      background-repeat: no-repeat;
      background-position: calc(100% - 15px) 0;
      height: 364px;
      @include sm-block() {
        height: auto;
      }
    }
    h3 {
      font-size: 22px;
      margin-bottom: 18px;
      @include xs-block() {
        text-align: center;
      }
    }
    ul {
      font-size: 18px;
    }
    li {
      display: flex;
      align-items: center;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
      &::before {
        content: '';
        display: block;
        @include triangle(to right, 11px, 14px, $color-accent-second);
        margin-right: 11px;
      }
    }
  }
  &__inner {
    width: 555px;
    @include lg-block() {
      width: 100%;
    }
  }
  &__icon {
    width: 108px;
    height: 108px;
    background-image: url(../img/features-item-icon-bg.jpg);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    @include xs-block() {
      margin: 0 auto;
    }
  }
  &__icon-1 {
    @include retina-sprite($features-item-1-group);
  }
  &__icon-2 {
    @include retina-sprite($features-item-2-group);
  }
  &__icon-3 {
    @include retina-sprite($features-item-3-group);
  }
  &__icon-4 {
    @include retina-sprite($features-item-4-group);
  }
  &__icon-5 {
    @include retina-sprite($features-item-5-group);
  }
  &__icon-6 {
    @include retina-sprite($features-item-6-group);
  }
}