html {
  min-width: 320px;
  overflow: hidden;
}

body {
  font-family: "PT Sans", sans-serif;
  color: #333333;
  overflow-x: hidden;
}

button {
  border: none;
  background-color: transparent;
  padding: 0;
}

img {
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.animated {
  opacity: 0;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container-fluid {
  padding-left: 15px;
  padding-right: 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.is-hidden {
  display: none !important;
}

.clearfix {
  overflow: auto;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.button {
  display: inline-block;
  font-size: 24px;
  font-weight: 700;
  text-decoration: none;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all, 0.3s;
}

.button_accent {
  background-color: #f56444;
  color: #fff;
  padding: 15px 90px;
}

.button_accent:hover {
  background-color: #f4502c;
}

.button_light {
  font-size: 20px;
  background-color: #fff;
  color: inherit;
  padding: 15px 20px;
}

.button_light:hover {
  background-color: #f2f2f2;
}

.title {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  text-align: center;
}

.title h2 {
  font-size: 42px;
  margin-top: 0;
}

.title h2 span {
  display: block;
  font-weight: 400;
}

.circle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 50%;
  color: #f56444;
  font-weight: 700;
}

.accordion li:not(:last-child) {
  margin-bottom: 12px;
}

.accordion__question {
  font-size: 22px;
  font-weight: 700;
  background-color: #ffba00;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 5px 22px 5px 14px;
  cursor: pointer;
}

.accordion__question::after {
  content: '';
  display: block;
  margin-top: 7px;
  margin-left: auto;
  flex-shrink: 0;
  border-style: solid;
  border-width: 12px 7px 0 7px;
  border-color: #fff rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  width: 0;
  height: 0;
}

.accordion__question::before {
  content: '?';
  box-sizing: border-box;
  display: inline-flex;
  width: 36px;
  height: 36px;
  align-items: center;
  justify-content: center;
  font-family: "Open Sans", sans-serif;
  font-size: 22px;
  border: 2px dashed #fff;
  border-radius: 50%;
  flex-shrink: 0;
}

.accordion__question p {
  margin: 0;
  padding: 0 15px;
}

.accordion__answer {
  display: none;
  font-size: 18px;
  background-color: #fff;
  margin: 8px 0 0;
  padding: 8px 17px 10px;
}

.arrow {
  width: 29px;
  height: 29px;
  position: absolute;
  top: 50%;
  background-color: #ffba00;
  border-radius: 3px;
  background-image: url(../img/arrow.png);
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
  z-index: 1;
}

.arrow_next {
  right: -15px;
  transform: translate3d(0, -50%, 0);
}

.arrow_prev {
  left: -15px;
  transform: translate3d(0, -50%, 0) rotate(180deg);
}

.arrow.slick-disabled {
  background-color: #dcdcdc;
}

.preloader__inner label:nth-child(1) {
  animation: loader 3s 600ms infinite ease-in-out;
}

.preloader__inner label:nth-child(2) {
  animation: loader 3s 500ms infinite ease-in-out;
}

.preloader__inner label:nth-child(3) {
  animation: loader 3s 400ms infinite ease-in-out;
}

.preloader__inner label:nth-child(4) {
  animation: loader 3s 300ms infinite ease-in-out;
}

.preloader__inner label:nth-child(5) {
  animation: loader 3s 200ms infinite ease-in-out;
}

.preloader__inner label:nth-child(6) {
  animation: loader 3s 100ms infinite ease-in-out;
}

.preloader {
  position: fixed;
  background: #c5e7f8;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: opacity 0.55s, visibility 0.55s;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
}

.preloader img {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.preloader_active {
  opacity: 1;
  visibility: visible;
}

.preloader__inner {
  transform: translateY(-50%);
  top: 50%;
  position: absolute;
  width: calc(100% - 200px);
  color: #FFF;
  padding: 0 100px;
  text-align: center;
}

.preloader__inner label {
  font-size: 2em;
  padding: 1%;
  opacity: 0;
  display: inline-block;
}

.preloader__inner label:after {
  content: '•';
}

@keyframes loader {
  0% {
    opacity: 0;
    transform: translateX(-300px) scale(1);
  }

  33% {
    opacity: 1;
    transform: translateX(0px) scale(2);
  }

  66% {
    opacity: 1;
    transform: translateX(0px) scale(1);
  }

  100% {
    opacity: 0;
    transform: translateX(300px) scale(2);
  }
}

.header {
  width: 100%;
  position: fixed;
  z-index: 9;
  top: 0;
}

.header__container {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  background-color: #fff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  align-items: center;
  padding: 16px 28px;
}

.header__logo {
  margin-right: 64px;
}

.header__menu {
  display: block;
  transition: all, 0.3s;
}

.header__menu ul.animated {
  opacity: 1;
}

.header__menu li {
  display: inline-block;
}

.header__menu li:not(:last-child) {
  margin-right: 30px;
}

.header__menu a {
  color: inherit;
  text-decoration: none;
}

.header__menu a:hover,
.header__menu a.is_active {
  color: #f56444;
  text-decoration: underline;
}

.header__menu.animated {
  opacity: 1;
}

.header__trigger {
  display: none;
  transition: all 0.2s;
  background: transparent;
  width: 26px;
  height: 26px;
}

.header__trigger span,
.header__trigger span::before,
.header__trigger span::after {
  display: block;
  transition: all 0.2s;
  width: 26px;
  height: 3px;
  background: #f56444;
  border-radius: 20px;
  border: none;
}

.header__trigger span {
  position: relative;
  margin-top: 0;
  margin-left: 0;
}

.header__trigger span::before {
  content: "";
  top: -8px;
  left: 0;
  position: absolute;
}

.header__trigger span::after {
  content: "";
  top: 8px;
  left: 0;
  position: absolute;
}

.header__trigger_active span {
  transform: rotate(-45deg);
}

.header__trigger_active span::after {
  top: 0;
  transform: rotate(90deg);
}

.header__trigger_active span::before {
  display: none;
}

