  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin: 2px auto 0;
  display: flex;
  justify-content: space-between;
}

.steps__item {
  text-align: center;
  color: #fff;
}

.steps__header {
  position: relative;
  margin: 0 auto 37px;
  max-width: 165px;
}

.steps__number {
