.products__img {
  display: block;
  max-width: 460px;
  margin: 0 auto 22px;
  position: relative;
  cursor: pointer;
}

.products__img a {
  display: block;
  position: relative;
}

.products__img a::after {
  content: '';
  display: flex;
  opacity: 0;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: linear-gradient(to right, transparent 43%, #c5e7f8 43%, #c5e7f8 57%, transparent 57%) rgba(255, 255, 255, 0.5);
  transition: all 0.3s;
  z-index: 1;
}

.products__img a::before {
  content: '';
  display: block;
  opacity: 0;
  background-image: url(../img/sprites.png);
  background-position: -130px -66px;
  width: 35px;
  height: 35px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  transition: all 0.3s;
  z-index: 2;
}

.products__img a:hover::after,
.products__img a:hover::before {
  opacity: 1;
}

.products__img img {
  display: block;
}

.products__footer {
