.services {
  background-image: url(../img/services-bg.jpg);
  background-color: #F9F9F9;
  background-repeat: no-repeat;
  background-position: center 0;
  padding: 43px 0 59px;
  @include md-block() {
    background-size: cover;
  }
  .title {
    margin-bottom: 45px;
    h2 {
      margin-bottom: 0;
    }
    span {
      font-size: 30px;
      @include xs-block() {
        font-size: 20px;
      }
    }
    p {
      font-size: 24px;
      font-weight: 700;
      margin: 11px 0 39px;
      @include sm-block() {
        font-size: 20px;
      }
    }
    h3 {
      font-size: 30px;
      margin: 0;
      @include sm-block() {
        font-size: 24px;
      }
    }
  }
  &__container {
    @include col();
    @include size-md(12);
    &_left {
      @include size-xl(4);
      @include size-lg(6);
      @include shift-xl(1);
      @include shift-lg(0);
      @include md-block() {
        margin-bottom: 16px;
      }
    }
    &_right {
      @include size-xl(5);
      @include size-lg(6);
    }
  }
  &__items {
    li {
      display: flex;
      align-items: center;
      font-size: 18px;
      background-color: #fff;
      border-radius: 3px;
      padding: 5px 18px;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
      &::before {
        content: '';
        display: block;
        @include triangle(to right, 10px, 10px, $color-accent-second);
        margin-right: 11px;
      }
    }
  }
}