.products {
  padding: 71px 0 31px;
  .title {
    margin-bottom: 22px;
  }
  &__card {
    @include col();
    @include size(6);
    @include size-md(12);
    background-color: #f9f9f9;
    padding: 30px 20px 50px;
    margin-bottom: 30px;
    @include lg-block() {
      padding: 30px 20px;
    }
    @include xs-block() {
      padding: 20px 15px;
    }
  }
  &__header {
    font-family: $font-second;
    text-align: center;
    margin-bottom: 21px;
    h3 {
      font-size: 22px;
      font-weight: 700;
      margin: 0;
      @include xs-block() {
        font-size: 20px;
      }
    }
    p {
      margin: 0;
    }
  }
  &__img {
    display: block;
    max-width: 460px;
    margin: 0 auto 22px;
    position: relative;
    cursor: pointer;
    a {
      display: block;
      position: relative;
      &::after {
        content: '';
        display: flex;
        opacity: 0;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: linear-gradient(to right, transparent 43%, #c5e7f8 43%, #c5e7f8 57%, transparent 57%)
                    rgba(255, 255, 255, 0.5);
        transition: all 0.3s;
        z-index: 1;
      }
      &::before {
        content: '';
        display: block;
        opacity: 0;
        @include retina-sprite($loupe-icon-group);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
        transition: all 0.3s;
        z-index: 2;
      }
      &:hover {
        &::after, &::before {
          opacity: 1;
        }
      }
    }
    @include md-block() {
      max-width: none;
    }
    img {
      display: block;
      @include md-block() {
        width: 100%;
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    padding: 0 32px;
    @include lg-block() {
      padding: 0;
    }
    @include xs-block() {
      flex-direction: column;
    }
  }
  &__description {
    font-family: $font-second;
    font-size: 18px;
    @include lg-block() {
      font-size: 16px;
    }
    @include xs-block() {
      margin-bottom: 15px;
    }
    >div {
      display: flex;
      align-items: center;
      &:not(:last-child) {
        margin-bottom: 9px;
      }
    }
    .icon {
      margin-right: 14px;
      flex-shrink: 0;
    }
    span {
      width: 100%;
      background-color: #e9e9e9;
      padding: 3px 11px;
      border-radius: 3px;
    }
  }
  &__raiting {
    font-family: $font-second;
    text-align: center;
    p {
      font-size: 18px;
      margin-top: 0;
    }
    span {
      font-size: 14px;
      font-weight: 300;
      strong {
        font-size: 33px;
        font-weight: 700;
        color: $color-accent;
      }
    }
  }
  &__hidden {
    display: none;
  }
  &_next {
    @include xs-block() {
      right: -15px;
    }
  }
  &_prev {
    @include xs-block() {
      left: -15px;
    }
  }
}

.loupe-icon {
  @include retina-sprite($loupe-icon-group);
}
.price-icon {
  @include retina-sprite($price-icon-group);
}
.save-icon {
  @include retina-sprite($save-icon-group);
}
.time-icon {
  @include retina-sprite($time-icon-group);
}