*:focus {
  outline: none;
}
html {
  min-width: 320px;
  overflow: hidden;
}
body {
  font-family: $font-main;
  //font-size: 16px;
  color: $color-main;
  overflow-x: hidden;
}
button {
  border: none;
  background-color: transparent;
  padding: 0;
}
img {
  max-width: 100%;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
}
.animated {
  opacity: 0;
}
.container {
  @include container();
  @include xs(max-width, 100%);
  @include sm(max-width, 540px);
  @include md(max-width, 720px);
  @include lg(max-width, 960px);
}
.container-fluid {
  @include container-full();
}
.row {
  @include row-flex();
}