.producers {
  padding: 72px 0 109px;
  .title {
    margin-bottom: 25px;
  }
  &__slider {
    @include col();
    @include size(12);
  }
  &__slide {
    margin: 0 15px;
    img {
      margin: 0 auto;
      filter: grayscale(100%) opacity(60%);
      transition: all, 0.3s;
      &:hover {
        filter: grayscale(0) opacity(100%);
      }
    }
  }
}