.button {
  display: inline-block;
  font-size: 24px;
  font-weight: 700;
  text-decoration: none;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all, 0.3s;
  &_accent {
    background-color: $color-accent;
    color: #fff;
    padding: 15px 90px;
    @include xs-block() {
      padding: 15px 40px;
    }
    &:hover {
      background-color: darken($color-accent, 5);
    }
  }
  &_light {
    font-size: 20px;
    background-color: #fff;
    color: inherit;
    padding: 15px 20px;
    @media screen and (max-width:375px) {
      padding: 15px;
    }
    &:hover {
      background-color: darken(#fff, 5);
    }
  }
}