.hero {
  background-image: url(../img/hero-bg.jpg);
  background-color: #C5E7F8;
  background-position: 50% 0;
  background-repeat: no-repeat;
  padding: 90px 0 37px;
  @include lg-block() {
    background-size: cover;
  }
  &__offer {
    text-align: center;
    margin-top: 24px;
    @include xs-block() {
      padding: 0 15px;
    }
    h1 {
      font-size: 50px;
      line-height: 1.24;
      margin: 0 0 18px;
      @include sm-block() {
        font-size: 40px;
      }
      @include xs-block() {
        font-size: 32px;
      }
      span {
        display: block;
        font-size: 34px;
        font-weight: 400;
        @include sm-block() {
          font-size: 24px;
        }
        @include xs-block() {
          font-size: 20px;
        }
      }
    }
    p {
      font-size: 20px;
      margin: 0 0 29px;
      @include sm-block() {
        font-size: 18px;
      }
      @include xs-block() {
        font-size: 16px;
      }
    }
  }
  &__features {
    width: 100%;
    margin-top: 35px;
    h2 {
      margin: 0 0 39px;
      font-size: 22px;
      text-align: center;
    }
    .button {
      display: flex;
      align-items: center;
      width: max-content;
      margin: 0 auto;
      font-family: $font-second;
      font-weight: 400;
    }
  }
  &__col {
    @include col();
    @include size-md(6);
    @include size-sm(12);
    padding-left: 15px;
    &:not(:last-child) {
      @include sm-block() {
        margin-bottom: 18px;
      }
    }
  }
  &__features-container {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 44px;
  }
  &__features-item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }
  .icon {
    margin-right: 25px;
    flex-shrink: 0;
  }
  &__icon-1 {
    @include retina-sprite($hero-item-1-group);
  }
  &__icon-2 {
    @include retina-sprite($hero-item-2-group);
  }
  &__icon-3 {
    @include retina-sprite($hero-item-3-group);
  }
  &__icon-4 {
    @include retina-sprite($hero-item-4-group);
  }
  &__icon-5 {
    @include retina-sprite($hero-item-5-group);
  }
  &__icon-6 {
    @include retina-sprite($hero-item-6-group);
  }
  &__video-icon {
    @include retina-sprite($video-icon-group);
    margin-right: 25px;
    @media screen and (max-width:375px) {
      display: none;
    }
  }
}