.steps {
  background-image: url(../img/steps-bg.jpg);
  background-color: #527F96;
  background-repeat: no-repeat;
  background-position: center 0;
  padding: 54px 0 77px;
  @include md-block() {
    background-size: cover;
  }
  .title {
    color: #fff;
  }
  &__container {
    @include col();
    @include size-xl(10);
    @include size-xl(10);
    @include size-lg(12);
    margin: 2px auto 0;
    display: flex;
    justify-content: space-between;
  }
  &__item {
    text-align: center;
    color: #fff;
    //max-width: 165px;
    //@include sm-block() {
    //  max-width: none;
    //}
  }
  &__header {
    position: relative;
    margin: 0 auto 37px;
    max-width: 165px;
    @include sm-block() {
      max-width: 50%;
      height: 125px;
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }
  }
  &__number {
    width: 94px;
    height: 94px;
    font-family: $font-second;
    font-size: 44px;
  }
  &__percent {
    width: 42px;
    height: 42px;
    position: absolute;
    top: -23px;
    right: calc(50% - 75px);
    @include sm-block() {
      top: 5px;
    }
  }
  &__footer {
    font-size: 18px;
    max-width: 165px;
    margin: 0 auto;
    @include sm-block() {
      max-width: 50%;
    }
    @include xs-block() {
      min-width: 75%;
    }
    p {
      font-weight: 700;
      margin: 0 0 13px;
    }
  }
}